body{
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App {
  text-align: center;
  background-color: black;
}

/* 計算空間 */
.calculate {
  color: white;
  font-size: 35pt;
  width: 95%;
  height: 80px;
  /* 圓角 */
  border-radius:25px; 
  margin: 3px;
  text-align: right;
}

/* 數字按鈕 */
.button{
  border:  1px solid #444444;
  background-color:#444444;
  color: white;
  font-size: large;
  width: 40px;
  height: 40px;
  /* 圓角 */
  border-radius:100px; 
  margin: 3px;
}

/* 數字按鈕點擊下去 */
.button:active { 
  border:  1px solid #444444;
  background-color:gray;
  color: white;
  font-size: large;
  width: 40px;
  height: 40px;
  /* 圓角 */
  border-radius:100px; 
  margin: 3px;
}

/* 按鈕0 */
.button0{
  border:  1px solid #444444;
  background-color:#444444;
  color: white;
  font-size: large;
  width: 90px;
  height: 40px;
  /* 圓角 */
  border-radius:100px; 
  margin: 3px;
  text-align:left;
  padding-left: 24px;
}

/* 按鈕0點擊下去 */
.button0:active{
  border:  1px solid #444444;
  background-color:gray;
  color: white;
  font-size: large;
width: 90px;
height: 40px;
  /* 圓角 */
  border-radius:100px; 
  margin: 3px;
  text-align:left;
  padding-left: 22px;
}

/* 加減乘除 */
.button_add {
  border:  1px solid#ffa131;
  background-color:#ffa131;
  color: white;
  font-size: large;
  width: 40px;
height: 40px;
  /* 圓角 */
  border-radius:100px; 
  margin: 3px;;
}

/* 加減乘除點擊下去 */
.button_add:active {
  border:  1px solid#ffa131;
  background-color:#ffbb00;
  color: white;
  font-size: large;
  width: 40px;
height: 40px;
  /* 圓角 */
  border-radius:100px; 
  margin: 3px;;
}

/* 其他按鈕 */
.button_ac {
  border:  1px solid#DDDD;
  background-color:#DDDD;
  color: black;
  font-size: large;
  width: 40px;
height: 40px;
  /* 圓角 */
  border-radius:100px; 
  margin: 3px;;
}
/* 其他按鈕點擊下去 */
.button_ac:active {
  border:  1px solid#DDDD;
  background-color:#f9f4f4f7;
  color: black;
  font-size: large;
  width: 40px;
height: 40px;
  /* 圓角 */
  border-radius:100px; 
  margin: 3px;;
}

